body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main-header {
  padding-top: 3rem;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 5;
  font-size: .9rem; }

a {
  color: #fff;
  text-decoration: none; }

.main-header a {
  font-weight: bold;
  letter-spacing: 1px; }

.social-icon {
  color: #fff; }

li {
  list-style: none; }

.main-menu.social {
  float: right;
  margin-right: 4rem; }

.main-menu {
  display: inline-block; }

.main-menu ul, .main-menu li {
  display: inline-block;
  padding: 0; }

.main-menu li {
  padding: 1rem;
  text-transform: uppercase; }

.container {
  padding: 0 15px;
  margin: 0 auto;
  width: 1170px;
  position: relative; }

.logo-container {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin-top: 1.2rem;
  top: 0; }

.logo-container a {
  color: #fff;
  font-size: 2rem; }

footer {
  padding: 1rem; }


.album-art.landing {
  width: 484px;
  height: 484px;
  padding: 3rem; }

.hero.store {
  background-image: url("https://scontent.fbed1-1.fna.fbcdn.net/v/t1.0-9/205603_1004680764979_9025_n.jpg?_nc_cat=105&_nc_ht=scontent.fbed1-1.fna&oh=a5ec2016e06a23d78741640867ec25aa&oe=5CB553CD");
  background-size: cover;
  background-position: 0% 60%;
  -webkit-filter: grayscale(100%) contrast(130%) brightness(50%);
          filter: grayscale(100%) contrast(130%) brightness(50%); }

.hero.contact {
  background-image: url("https://scontent.fbed1-2.fna.fbcdn.net/v/t1.0-9/299682_2083099401728_1147421824_n.jpg?_nc_cat=107&_nc_ht=scontent.fbed1-2.fna&oh=ad5229e3e4973034e0087ac004f45be3&oe=5CC5C4B2");
  background-size: cover;
  background-position: 50% 20%;
  -webkit-filter: grayscale(100%) contrast(130%) brightness(50%);
          filter: grayscale(100%) contrast(130%) brightness(50%); }

.contact-wrapper > div {
  padding: 0 3.5rem; }

.hero.tour {
  background-image: url("https://scontent.fbed1-2.fna.fbcdn.net/v/t1.0-9/200216_1002686072070_979_n.jpg?_nc_cat=102&_nc_ht=scontent.fbed1-2.fna&oh=2da484e736bd601aad2b1fc7de60e11b&oe=5CB6D886");
  background-size: 150% auto;
  -webkit-filter: blur(5px) grayscale(100%) contrast(120%) brightness(50%);
          filter: blur(5px) grayscale(100%) contrast(120%) brightness(50%);
  background-position: 0% 20%; }

.App {
  background-color: #243135;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  min-height: 100vh; }

.hero {
  height: 100vh;
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%; }

.center-container {
  margin: 0 auto;
  position: absolute;
  top: 25%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%); }
  .center-container.flex {
    display: flex;
    justify-content: center;
    align-items: center; }

.landing-container {
  padding-top: 15%;
  justify-content: center;
  margin-left: -15%;
  align-items: center; }

.main-body {
  min-height: 100vh; }

.mdc-button--outlined {
  border-color: #fff !important;
  color: #fff !important;
  margin-top: 1.2rem; }

.mdc-button::before, .mdc-button::after {
  background-color: #000; }

.mdc-floating-label--float-above {
  color: #000 !important; }

.mdc-line-ripple {
  background-color: #000 !important; }

.flex {
  display: flex; }

